'use client';

import { getProjectBySlug } from '@/lib/projects';
import Link from 'next/link';
import Image from 'next/image';
import Header from '@/components/header';
import ContactCTASection from '@/components/contactCTASection';
import Footer from '@/components/footer';

export default function ProjectDetailPage({ params }: { params: { slug: string } }) {
  const project = getProjectBySlug(params.slug);
  if (!project) return <div>Project Not Found</div>;

  return (
    <>
      <Header />

      <main className="bg-white pt-32">
        {/* Editorial Hero */}
        <section className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 mb-20">
          <p className="text-sm uppercase tracking-widest text-gray-500 mb-4">
            {project.category}
          </p>

          <h1 className="text-4xl md:text-5xl lg:text-6xl font-semibold text-gray-900 max-w-4xl leading-tight">
            {project.title}
          </h1>

          <p className="mt-6 text-lg text-gray-600 max-w-3xl">
            {project.description}
          </p>
        </section>

        {/* Hero Image */}
        <section className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 mb-28">
          <div className="relative w-full overflow-hidden rounded-2xl">
            <Image
              src={project.images[0]}
              alt={project.title}
              width={1600}
              height={900}
              className="w-full h-auto object-cover"
              priority
            />
          </div>
        </section>

        {/* Content */}
        <section className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 grid grid-cols-1 lg:grid-cols-12 gap-16">
          {/* Main Content */}
          <div className="lg:col-span-8">
            <h2 className="text-2xl font-semibold text-gray-900 mb-4">
              Overview
            </h2>
            <p className="text-lg text-gray-600 mb-12">
              {project.overview}
            </p>

            <h2 className="text-2xl font-semibold text-gray-900 mb-4">
              Impact
            </h2>
            <ul className="space-y-3 text-lg text-gray-600 list-disc list-inside">
              {project.impact.map((item: string, i: number) => (
                <li key={i}>{item}</li>
              ))}
            </ul>
          </div>

          {/* Sidebar */}
          <aside className="lg:col-span-4">
            <div className="sticky top-28 rounded-xl border border-gray-200 p-6">
              <h3 className="text-lg font-medium text-gray-900 mb-4">
                Tech Stack
              </h3>
              <div className="flex flex-wrap gap-2">
                {project.techStack.map((tech: string) => (
                  <span
                    key={tech}
                    className="text-sm px-3 py-1 rounded-full bg-gray-100 text-gray-700"
                  >
                    {tech}
                  </span>
                ))}
              </div>
            </div>
          </aside>
        </section>

        {/* Gallery */}
        <section className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 mt-32">
          <h2 className="text-2xl font-semibold text-gray-900 mb-10">
            Gallery
          </h2>

          <div className="columns-1 sm:columns-2 lg:columns-3 gap-8 space-y-8">
            {project.images.map((img: string, index: number) => (
              <div key={index} className="break-inside-avoid">
                <Image
                  src={img}
                  alt={`${project.title} screenshot ${index + 1}`}
                  width={800}
                  height={1000}
                  className="w-full h-auto rounded-xl"
                />
              </div>
            ))}
          </div>
        </section>

        <ContactCTASection />
      </main>

      <Footer />
    </>
  );
}
